body {
  margin: 0;
  font-family: "Euclid Circular A";
  color: #1d1d1d;
  background: #e3e1cf;
  overflow-x: hidden;
}

h2 {
  font-size: 20px;
  font-weight: 200;
}

p,
h2 {
  position: relative;
  z-index: 1;
  margin: 20px 15%;
  text-align: left;
}

p {
  opacity: 0.8;
  text-align: left;
  font-size: 18px;
}

section {
  padding: 40px 0;
}

.image {
  height: 380px;
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
  background-image: url("../assets/newbg.jpeg");
}

.text {
  position: relative;
}

.text::before {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 150%;
  height: 125%;
  background: #e3e1cf;
  rotate: -3deg;
  translate: -50% -50%;
}

/* Media queries for responsive design */
@media (max-width: 768px) {
  body {
    font-size: 16px;
  }

  h2 {
    font-size: 18px;
  }

  p {
    font-size: 16px;
  }

  .image {
    height: 280px;
  }

  .text::before {
    width: 130%;
    height: 110%;
  }
}

@media (max-width: 480px) {
  body {
    font-size: 14px;
  }

  h2 {
    font-size: 20px;
  }

  p {
    font-size: 18px;
  }

  .image {
    height: 200px;
  }

  .text::before {
    width: 120%;
    height: 100%;
  }
}
.notion-link{
  color: inherit;
  word-break: break-word;
  text-decoration: inherit;
  border-bottom: .05em solid;
  border-color: var(--fg-color-2);
  transition: border-color .1s ease-in,opacity .1s ease-in;
}

.notion-link:hover{
  border-color:var(--fg-color-6);
  
}

